import React , {useState} from 'react';
// import React,{useState} from 'react';
import Avatar from '@material-ui/core/Avatar';
import Button from '@material-ui/core/Button';
import CssBaseline from '@material-ui/core/CssBaseline';
import TextField from '@material-ui/core/TextField';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';
import Link from '@material-ui/core/Link';
import Grid from '@material-ui/core/Grid';
import Box from '@material-ui/core/Box';
import LockOutlinedIcon from '@material-ui/icons/LockOutlined';
import Typography from '@material-ui/core/Typography';
import { makeStyles } from '@material-ui/core/styles';
import Container from '@material-ui/core/Container';
import { Link as RouterLink, withRouter } from 'react-router-dom';
import firebase from '@firebase/app'
import 'firebase/database';
import 'firebase/auth'

const firebaseConfig = {
  apiKey: "AIzaSyDmxdelKoI9a_OJSymS4Qj_v7Lsilinqt8",
  authDomain: "login-40e4f.firebaseapp.com",
  projectId: "login-40e4f",
  storageBucket: "login-40e4f.appspot.com",
  messagingSenderId: "931035855176",
  appId: "1:931035855176:web:6ef599723c6f65ce09db7d"
};


firebase.initializeApp(firebaseConfig);

function Copyright() {
  return (
    <Typography variant="body2" color="textSecondary" align="center">
      {'Copyright © '}
      <Link color="inherit" href="https://material-ui.com/">
        Your Website
      </Link>{' '}
      {new Date().getFullYear()}
      {'.'}
    </Typography>
  );
}
const MyLink = React.forwardRef((props, ref) => (
  <RouterLink ref={ref} to="/getting-started/installation/" {...props} />
));


const useStyles = makeStyles((theme) => ({
  paper: {
    marginTop: theme.spacing(8),
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
  },
  avatar: {
    margin: theme.spacing(1),
    backgroundColor: theme.palette.secondary.main,
  },
  form: {
    width: '100%', // Fix IE 11 issue.
    marginTop: theme.spacing(3),
  },
  submit: {
    margin: theme.spacing(3, 0, 2),
  },
}));

 const Signup = (props)  => {
  const classes = useStyles();

  
const [user, setUser] = useState ({
  name : '',
  email: '',
  password: '',
  avatar:'',
});

const handleChange = (e) =>{
   setUser({
     ...user,
     [e.target.name]: e.target.value
   });
};

const handleSubmit = (e) => {
  e.preventDefault();

  firebase.auth().createUserWithEmailAndPassword(user.email, user.password)
  .then(response => {
    delete user.password;
    firebase.database().ref(`/users/${response.user.uid}`).set(user);
    alert('Bienvenido a la home');
    props.history.push('/')
  })

 
  .catch(error => {
    console.log(error);
    alert(error.message);
    
  });
  
}
  return (
    <Container component="main" maxWidth="xs">
      <CssBaseline />
      <div className={classes.paper}>
        <Avatar className={classes.avatar}>
          <LockOutlinedIcon />
        </Avatar>
        <Typography component="h1" variant="h5">
          Sign up
        </Typography>
        <form className={classes.form} onSubmit={handleSubmit}>
          <Grid container spacing={2}>
            <Grid item xs={12} sm={6}>
              <TextField
                // autoComplete="fname"
                name="avatar"
                variant="outlined"
                required
                fullWidth
                id="avatar"
                label="URL avatar"
                autoFocus
                value={user.avatar}
                onChange={handleChange}
              />
            </Grid>
            <Grid item xs={12} sm={6}>
              <TextField
                variant="outlined"
                required
                fullWidth
                id="name"
                label="Full Name"
                name="name"
                autoComplete="lname"
                value={user.name}
                onChange={handleChange}
              />
            </Grid>
            <Grid item xs={12}>
              <TextField
                variant="outlined"
                required
                fullWidth
                id="email"
                label="Email Address"
                name="email"
                autoComplete="email"
                value={user.email}
                onChange={handleChange}
              />
            </Grid>
            <Grid item xs={12}>
              <TextField
                variant="outlined"
                required
                fullWidth
                name="password"
                label="Password"
                type="password"
                id="password"
                autoComplete="current-password"
                value={user.password}
                onChange={handleChange}
              />
            </Grid>
            <Grid item xs={12}>
              {/* <FormControlLabel
                // control={<Checkbox value="allowExtraEmails" color="primary" />}
                // label="I want to receive inspiration, marketing promotions and updates via email."
              /> */}
            </Grid>
          </Grid>
          <Button
            type="submit"
            fullWidth
            variant="contained"
            color="secondary"
            className={classes.submit}
          >
            Sign Up
          </Button>
          <Grid container justifyContent="flex-end">
            <Grid item>
            {/* <Link to="/singup" component={MyLink} variant="body2">
                {"Don't have an account? Sign Up"}
              </Link> */}
              <Link to="/login" component={MyLink} variant="body2">
                Ya tienes cuenta? Sign in
              </Link>
            </Grid>
          </Grid>
        </form>
      </div>
      <Box mt={5}>
        
      </Box>
    </Container>
  );
}

export default withRouter(Signup);